<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
    <!--  ID -->
    <el-form-item label="ID" prop="ind_id">
      <el-input @keydown="recheck" v-model="ruleForm.ind_id"></el-input>
      <el-button @click="checkID"></el-button>
    </el-form-item>
    <!--  PW -->
    <el-form-item label="Password" prop="ind_password">
      <el-input
        type="password"
        v-model="ruleForm.ind_password"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <!--  PW  -->
    <el-form-item label="Password Confirmation" prop="ind_password_cf">
      <el-input
        type="password"
        v-model="ruleForm.ind_password_cf"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <!--  -->
    <el-form-item label="Name" prop="ind_name">
      <el-input v-model="ruleForm.ind_name"></el-input>
    </el-form-item>
    <!--   -->
    <el-form-item label="Birth" prop="ind_birth">
      <el-input
        v-model="ruleForm.ind_birth"
        placeholder="YYMMDD "
      ></el-input>
    </el-form-item>
    <!--  -->
    <el-form-item label="Gender" prop="ind_gender">
      <el-radio-group v-model="ruleForm.ind_gender">
        <el-radio label="M"></el-radio>
        <el-radio label="F"></el-radio>
      </el-radio-group>
    </el-form-item>
    <!--  -->
    <el-form-item label="Tel" prop="ind_phone">
      <el-input type="tel" v-model="ruleForm.ind_phone"></el-input>
    </el-form-item>
    <!--  -->
    <el-form-item label="Email" prop="ind_email">
      <el-input type="email" v-model="ruleForm.ind_email"></el-input>
    </el-form-item>
    <!--     -->
    <el-form-item>
      <el-button @click="resetForm('ruleForm')">Reset</el-button>
      <el-button
        type="warning"
        @click="SignupInd('ruleForm')"
        v-loading.fullscreen.lock="fullscreenLoading"
        >Create</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
export default {
  // data
  data() {
    //  
    const checkPWCF = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("()  "));
      } else if (value !== this.ruleForm.ind_password) {
        callback(new Error("( "));
      } else {
        callback();
      }
    };
    //   
    const validationID = (rule, value, callback) => {
      // this.checkID();
      if (value === "") {
        callback(new Error("ID "));
      } else if (length.value < 5 || length.value > 10) {
        callback(new Error("1-15 "));
      } else {
        callback();
      }
    };
    return {
      filled: false,
      allowedID: false,
      ruleForm: {
        ind_id: "",
        ind_password: "",
        ind_password_cf: "",
        ind_name: "",
        ind_birth: "",
        ind_gender: "",
        ind_phone: "",
        ind_email: "",
      },
      rules: {
        // 
        ind_id: [
          {
            validator: validationID,
            trigger: "blur",
          },
          {
            min: 5,
            max: 10,
            message: "5-10 ",
            trigger: "blur",
          },
        ],
        // 
        ind_password: [
          {
            required: true,
            message: " ",
            trigger: "blur",
          },
          {
            min: 5,
            max: 10,
            message: "5-10 ",
            trigger: "blur",
          },
        ],
        // 
        ind_password_cf: [
          {
            required: true,
            message: " ",
            trigger: "blur",
          },
          {
            min: 5,
            max: 10,
            message: "5-10 ",
            trigger: "blur",
          },
          { validator: checkPWCF, trigger: "blur" },
        ],
        // 
        ind_name: [
          {
            required: true,
            message: " ",
            trigger: "blur",
          },
        ],
        // 
        ind_birth: [
          {
            required: true,
            message: " ",
            trigger: "blur",
          },
          {
            min: 6,
            max: 6,
            message: "'YYMMDD'  ",
            trigger: "blur",
          },
        ],
        // 
        ind_gender: [
          {
            required: true,
            message: " ",
            trigger: "change",
          },
        ],
        // 
        ind_phone: [
          {
            required: true,
            message: " ",
            trigger: "change",
          },
        ],
        // 
        ind_email: [
          {
            required: true,
            message: " ",
            trigger: "change",
          },
          {
            type: "email",
            message: "  ",
            trigger: "change",
          },
        ],
      },
      fullscreenLoading: false,
    };
  },
  // watch
  watch: {},
  // computed
  computed: {},
  // method
  methods: {
    SignupInd(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid && this.allowedID) {
          console.log(this.ruleForm);
          this.openFullScreen2();
          //  
          axios
            .post("https://localhost:8443/ind", {
              ind_id: this.ruleForm.ind_id,
              ind_password: this.ruleForm.ind_password,
              ind_name: this.ruleForm.ind_name,
              ind_birth: this.ruleForm.ind_birth,
              ind_gender: this.ruleForm.ind_gender,
              ind_phone: this.ruleForm.ind_phone,
              ind_email: this.ruleForm.ind_email,
            })
            .then((res) => {
              if (res.status == 200) {
                this.$store.state.SignupDialogIndiv = false;
                console.log(this.ruleForm);
                setTimeout(() => {
                  this.successmessage();
                }, 3000);
              }
            })
            .catch((err) => {
              console.log(err);
            });
        } else if (this.allowedID == false) {
          console.log("error submit!!");
          this.recheckid();
          return false;
        } else {
          this.failed();
          return false;
        }
        //
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
      // 
    },
    openFullScreen2() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      setTimeout(() => {
        loading.close();
      }, 3000);
    },
    // 
    successmessage() {
      this.$message({
        message: "() ",
        type: "success",
      });
    },
    // 
    failed() {
      this.$message.error("  ");
    },
    // 
    recheckid() {
      this.$message.error("  ");
    },
    // id
    checkID() {
      // alert("");
      if (this.ruleForm.ind_id.length > 4 && this.ruleForm.ind_id.length < 11) {
        axios
          .post("https://localhost:8443/ind/checkid", {
            ind_id: this.ruleForm.ind_id,
          })
          .then((res) => {
            console.log(res);
            this.$message.error("    .");
            this.ruleForm.ind_id = "";
            this.allowedID = false;
          })
          .catch((err) => {
            console.log(err);
            this.allowedID = true;
            this.$message({
              message: " .",
              type: "success",
            });
          });
      } else {
        this.failed();
      }
    },
    recheck() {
      this.allowedID = false;
    },
  },
};
</script>

<style></style>
