<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
    <!--  -->
    <el-form-item label="Company Name" prop="ent_name">
      <el-input v-model="ruleForm.ent_name"></el-input>
    </el-form-item>
    <!--  ID -->
    <el-form-item label="ID" prop="ent_id">
      <el-input v-model="ruleForm.ent_id" @keydown="recheck"></el-input>
      <el-button @click="checkID"></el-button>
    </el-form-item>
    <!--  PW -->
    <el-form-item label="Password" prop="ent_password">
      <el-input type="password" v-model="ruleForm.ent_password"></el-input>
    </el-form-item>
    <!--  PW  -->
    <el-form-item label="Password Confirmation" prop="ent_password_cf">
      <el-input
        type="password"
        v-model="ruleForm.ent_password_cf"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <!--  -->
    <el-form-item label="Tel" prop="ent_contact">
      <el-input type="tel" v-model="ruleForm.ent_contact"></el-input>
    </el-form-item>
    <!--  -->
    <el-form-item label="Email" prop="ent_email">
      <el-input type="email" v-model="ruleForm.ent_email"></el-input>
    </el-form-item>
    <!--     -->
    <el-form-item>
      <el-button @click="resetForm('ruleForm')">Reset</el-button>
      <el-button
        type="warning"
        @click="submitForm('ruleForm')"
        v-loading.fullscreen.lock="fullscreenLoading"
        >Create</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
export default {
  data() {
    //  
    const checkPWCF = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(" "));
      } else if (value !== this.ruleForm.ent_password) {
        callback(new Error("()  "));
      } else {
        callback();
      }
    };
    //   
    // const validationID = (rule, value, callback) => {
    //   // this.checkID();
    //   if (value === "") {
    //     callback(new Error("ID "));
    //   } else if (length.value < 5 || length.value > 10) {
    //     callback(new Error("1-15 "));
    //   } else {
    //     callback();
    //   }
    // };
    //   
    // const validationID = (rule, value, callback) => {
    //   // this.checkID();
    //   if (value === "") {
    //     callback(new Error("ID "));
    //   } else if (length.value < 5 || length.value > 10) {
    //     callback(new Error("1-15 "));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      allowedID: false,
      ruleForm: {
        ent_name: "",
        ent_id: "",
        ent_password: "",
        ent_password_cf: "",
        ent_contact: "",
        ent_email: "",
        open: false,
      },
      rules: {
        ent_name: [
          {
            required: true,
            message: " ",
            trigger: "blur",
          },
        ],
        ent_id: [
          {
            required: true,
            message: "  ",
            trigger: "blur",
          },
          {
            min: 5,
            max: 10,
            message: "Length should be 5 to 10",
            trigger: "blur",
          },
        ],
        ent_password: [
          {
            required: true,
            message: " ",
            trigger: "blur",
          },
          {
            min: 5,
            max: 10,
            message: "5-10 ",
            trigger: "blur",
          },
        ],
        ent_password_cf: [
          {
            min: 5,
            max: 10,
            message: "5-10 ",
            trigger: "blur",
          },
          { validator: checkPWCF, trigger: "blur" },
        ],
        ent_contact: [
          {
            required: true,
            message: " ",
            trigger: "change",
          },
        ],
        ent_email: [
          {
            required: true,
            message: " ",
            trigger: "change",
          },
          {
            type: "email",
            message: "  ",
            trigger: "change",
          },
        ],
      },
      fullscreenLoading: false,
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid && this.allowedID) {
          this.openFullScreen2();
          this.$store.state.SignupDialogCompany = false;
          //  
          axios
            .post("https://localhost:8443/ent", {
              ent_id: this.ruleForm.ent_id,
              ent_password: this.ruleForm.ent_password,
              ent_name: this.ruleForm.ent_name,
              ent_contact: this.ruleForm.ent_contact,
              ent_email: this.ruleForm.ent_email,
            })
            .then((res) => {
              if (res.status == 200) {
                this.$store.state.SignupDialogCompany = false;
                setTimeout(() => {
                  this.successmessage();
                }, 3000);
                console.log(this.ruleForm);
              }
            })
            .catch((err) => {
              console.log(err);
            });
        } else if (this.allowedID == false) {
          console.log("error submit!!");
          this.recheckid();
          return false;
        } else {
          this.failed();
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    openFullScreen2() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      setTimeout(() => {
        loading.close();
      }, 3000);
    },
    successmessage() {
      this.$message({
        message: "() ",
        type: "success",
      });
    },
    failed() {
      this.$message.error(" ");
    },
    // 
    recheckid() {
      this.$message.error("  ");
    },
    // id
    checkID() {
      // alert("");
      if (this.ruleForm.ent_id.length > 4 && this.ruleForm.ent_id.length < 11) {
        axios
          .post("https://localhost:8443/ent/checkid", {
            ent_id: this.ruleForm.ent_id,
          })
          .then((res) => {
            console.log(res);
            this.$message.error("    .");
            this.ruleForm.ent_id = "";
            this.allowedID = false;
          })
          .catch((err) => {
            console.log(err);
            this.allowedID = true;
            this.$message({
              message: " .",
              type: "success",
            });
          });
      } else {
        this.failed();
      }
    },
    recheck() {
      this.allowedID = false;
    },
  },
};
</script>

